import React, { ButtonHTMLAttributes } from 'react';

interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: 'primary' | 'secondary' | 'danger' | 'success' | 'warning';
  size?: 'sm' | 'md' | 'lg';
  icon?: React.ReactNode;
  iconPosition?: 'left' | 'right';
}

const Button: React.FC<ButtonProps> = ({
  children,
  variant = 'primary',
  size = 'md',
  icon,
  iconPosition = 'left',
  className = '',
  ...props
}) => {
  const baseClasses = 'inline-flex items-center justify-center rounded-md font-medium transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2 border';
  
  const variantClasses = {
    primary: 'bg-[#7973BB] hover:bg-[#7973BB]/90 text-white focus:ring-[#7973BB] border-[#7973BB]',
    secondary: 'bg-white hover:bg-gray-100 text-[#7973BB] focus:ring-[#7973BB] border-[#7973BB]',
    danger: 'bg-red-600 hover:bg-red-700 text-white focus:ring-red-500 border-[#7973BB]',
    success: 'bg-green-600 hover:bg-green-700 text-white focus:ring-green-500 border-[#7973BB]',
    warning: 'bg-amber-500 hover:bg-amber-600 text-white focus:ring-amber-500 border-[#7973BB]'
  };
  
  const sizeClasses = {
    sm: 'px-2 py-1 text-sm',
    md: 'px-4 py-2',
    lg: 'px-6 py-3 text-lg'
  };
  
  const disabledClasses = props.disabled ? 'opacity-50 cursor-not-allowed' : '';
  
  return (
    <button
      className={`${baseClasses} ${variantClasses[variant]} ${sizeClasses[size]} ${disabledClasses} ${className}`}
      {...props}
    >
      {icon && iconPosition === 'left' && <span className="mr-1">{icon}</span>}
      {children}
      {icon && iconPosition === 'right' && <span className="ml-1">{icon}</span>}
    </button>
  );
};

export default Button;