import React, { createContext, useContext, useReducer, ReactNode, useEffect } from 'react';
import { Guest, Table, Constraint, Assignment, SeatingPlan, UserSubscription } from '../types';
import { getMaxGuestLimit, isPremiumSubscription } from '../utils/premium';
import { supabase, supabaseConfigured } from '../lib/supabase';
import { loadRecentSessionSettings, clearRecentSessionSettings } from '../lib/sessionSettings';

interface AppState {
  guests: Guest[];
  tables: Table[];
  constraints: Record<string, Record<string, 'must' | 'cannot' | ''>>;
  adjacents: Record<string, string[]>;
  assignments: Record<string, string>;
  seatingPlans: SeatingPlan[];
  currentPlanIndex: number;
  subscription: UserSubscription | null;
  userSetTables: boolean;
  user: any | null;
  loadedSavedSetting: boolean;
}

type AppAction =
  | { type: 'ADD_GUESTS'; payload: Guest[] }
  | { type: 'REMOVE_GUEST'; payload: number }
  | { type: 'RENAME_GUEST'; payload: { index: number; name: string } }
  | { type: 'CLEAR_GUESTS' }
  | { type: 'SET_CONSTRAINT'; payload: { guest1: string; guest2: string; value: 'must' | 'cannot' | '' } }
  | { type: 'SET_ADJACENT'; payload: { guest1: string; guest2: string } }
  | { type: 'REMOVE_ADJACENT'; payload: { guest1: string; guest2: string } }
  | { type: 'ADD_TABLE'; payload?: Partial<Table> }
  | { type: 'REMOVE_TABLE'; payload: number }
  | { type: 'UPDATE_TABLE_SEATS'; payload: { id: number; seats: number } }
  | { type: 'UPDATE_TABLE_NAME'; payload: { id: number; name?: string } }
  | { type: 'UPDATE_ASSIGNMENT'; payload: { name: string; tables: string } }
  | { type: 'SET_SEATING_PLANS'; payload: SeatingPlan[] }
  | { type: 'SET_CURRENT_PLAN_INDEX'; payload: number }
  | { type: 'RESET' }
  | { type: 'IMPORT_STATE'; payload: Partial<AppState> }
  | { type: 'SET_SUBSCRIPTION'; payload: UserSubscription | null }
  | { type: 'SET_USER'; payload: any }
  | { type: 'SET_USER_SET_TABLES'; payload: boolean }
  | { type: 'UPDATE_DEFAULT_TABLES'; payload: Table[] }
  | { type: 'SET_LOADED_SAVED_SETTING'; payload: boolean };

const STORAGE_KEY = 'seatyr_app_state';

const defaultTables: Table[] = Array.from({ length: 10 }, (_, i) => ({
  id: i + 1,
  seats: 8
}));

const initialState: AppState = {
  guests: [],
  tables: defaultTables,
  constraints: {},
  adjacents: {},
  assignments: {},
  seatingPlans: [],
  currentPlanIndex: 0,
  subscription: null,
  userSetTables: false,
  user: null,
  loadedSavedSetting: false
};

const loadSavedState = (): AppState => {
  try {
    const savedState = localStorage.getItem(STORAGE_KEY);
    if (savedState) {
      const parsedState = JSON.parse(savedState);
      // Don't load subscription from localStorage
      if (parsedState.subscription) {
        delete parsedState.subscription;
      }
      if (parsedState.user) {
        delete parsedState.user;
      }
      return { ...initialState, ...parsedState };
    }
  } catch (error) {
    console.error('Error loading saved state:', error);
  }
  return initialState;
};

const calculateRequiredTables = (guestCount: number, seatsPerTable: number = 8): Table[] => {
  const totalTablesNeeded = Math.ceil(guestCount / seatsPerTable);
  return Array.from({ length: totalTablesNeeded }, (_, i) => ({
    id: i + 1,
    seats: seatsPerTable
  }));
};

const reducer = (state: AppState, action: AppAction): AppState => {
  let newState: AppState;

  switch (action.type) {
    case 'ADD_GUESTS': {
      // Check if adding these guests would exceed the guest limit for non-premium users
      const newGuests = [...state.guests, ...action.payload];
      const totalGuests = newGuests.length;
      const guestLimit = getMaxGuestLimit(state.subscription);
      
      // Block adding guests if it would exceed the limit
      if (totalGuests > guestLimit) {
        console.log(`Cannot add guests: limit of ${guestLimit} exceeded (total: ${totalGuests})`);
        alert(`You can only have up to ${guestLimit} guests with your current plan. Upgrade to Premium for unlimited guests.`);
        return state;
      }
      
      let newTables = state.tables;
      if (!state.userSetTables) {
        const totalGuestCount = newGuests.reduce((sum, guest) => sum + guest.count, 0);
        newTables = calculateRequiredTables(totalGuestCount);
      }
      
      newState = {
        ...state,
        guests: newGuests,
        tables: newTables,
        seatingPlans: []
      };
      break;
    }
    
    case 'REMOVE_GUEST': {
      const newGuests = [...state.guests];
      const removedGuest = newGuests.splice(action.payload, 1)[0];
      
      const newConstraints = { ...state.constraints };
      delete newConstraints[removedGuest.name];
      Object.keys(newConstraints).forEach(guest => {
        delete newConstraints[guest][removedGuest.name];
      });
      
      const newAdjacents = { ...state.adjacents };
      delete newAdjacents[removedGuest.name];
      Object.keys(newAdjacents).forEach(guest => {
        newAdjacents[guest] = newAdjacents[guest]?.filter(adj => adj !== removedGuest.name) || [];
      });
      
      const newAssignments = { ...state.assignments };
      delete newAssignments[removedGuest.name];
      
      let newTables = state.tables;
      if (!state.userSetTables) {
        const totalGuests = newGuests.reduce((sum, guest) => sum + guest.count, 0);
        newTables = calculateRequiredTables(totalGuests);
      }
      
      newState = {
        ...state,
        guests: newGuests,
        constraints: newConstraints,
        adjacents: newAdjacents,
        assignments: newAssignments,
        tables: newTables,
        seatingPlans: []
      };
      break;
    }
    
    case 'RENAME_GUEST': {
      const newGuests = [...state.guests];
      const oldName = newGuests[action.payload.index].name;
      newGuests[action.payload.index] = {
        ...newGuests[action.payload.index],
        name: action.payload.name
      };
      
      const newConstraints = { ...state.constraints };
      if (newConstraints[oldName]) {
        newConstraints[action.payload.name] = { ...newConstraints[oldName] };
        delete newConstraints[oldName];
      }
      Object.keys(newConstraints).forEach(guest => {
        if (newConstraints[guest][oldName] !== undefined) {
          newConstraints[guest][action.payload.name] = newConstraints[guest][oldName];
          delete newConstraints[guest][oldName];
        }
      });
      
      const newAdjacents = { ...state.adjacents };
      if (newAdjacents[oldName]) {
        newAdjacents[action.payload.name] = [...newAdjacents[oldName]];
        delete newAdjacents[oldName];
      }
      Object.keys(newAdjacents).forEach(guest => {
        if (newAdjacents[guest]) {
          newAdjacents[guest] = newAdjacents[guest].map(adj => 
            adj === oldName ? action.payload.name : adj
          );
        }
      });
      
      const newAssignments = { ...state.assignments };
      if (newAssignments[oldName]) {
        newAssignments[action.payload.name] = newAssignments[oldName];
        delete newAssignments[oldName];
      }
      
      newState = {
        ...state,
        guests: newGuests,
        constraints: newConstraints,
        adjacents: newAdjacents,
        assignments: newAssignments,
        seatingPlans: []
      };
      break;
    }
    
    case 'CLEAR_GUESTS':
      newState = {
        ...state,
        guests: [],
        constraints: {},
        adjacents: {},
        assignments: {},
        seatingPlans: [],
        tables: defaultTables,
        userSetTables: false,
        loadedSavedSetting: false
      };
      
      // Clear recent session settings for premium users
      if (state.user && isPremiumSubscription(state.subscription)) {
        clearRecentSessionSettings(state.user.id, true);
      }
      
      break;
    
    case 'SET_CONSTRAINT': {
      const { guest1, guest2, value } = action.payload;
      const newConstraints = { ...state.constraints };
      
      if (!newConstraints[guest1]) newConstraints[guest1] = {};
      if (!newConstraints[guest2]) newConstraints[guest2] = {};
      
      newConstraints[guest1][guest2] = value;
      newConstraints[guest2][guest1] = value;
      
      if (value !== 'must') {
        const newAdjacents = { ...state.adjacents };
        if (newAdjacents[guest1]) {
          newAdjacents[guest1] = newAdjacents[guest1].filter(g => g !== guest2);
        }
        if (newAdjacents[guest2]) {
          newAdjacents[guest2] = newAdjacents[guest2].filter(g => g !== guest1);
        }
        newState = {
          ...state,
          constraints: newConstraints,
          adjacents: newAdjacents,
          seatingPlans: []
        };
        break;
      }
      
      newState = {
        ...state,
        constraints: newConstraints,
        seatingPlans: []
      };
      break;
    }
    
    case 'SET_ADJACENT': {
      const { guest1, guest2 } = action.payload;
      const newAdjacents = { ...state.adjacents };
      const newConstraints = { ...state.constraints };
      
      if (!newConstraints[guest1]) newConstraints[guest1] = {};
      if (!newConstraints[guest2]) newConstraints[guest2] = {};
      newConstraints[guest1][guest2] = 'must';
      newConstraints[guest2][guest1] = 'must';
      
      if (!newAdjacents[guest1]) newAdjacents[guest1] = [];
      if (!newAdjacents[guest2]) newAdjacents[guest2] = [];
      
      const isAlreadyAdjacent = newAdjacents[guest1].includes(guest2);
      
      if (!isAlreadyAdjacent) {
        if (newAdjacents[guest1].length < 2 && newAdjacents[guest2].length < 2) {
          newAdjacents[guest1].push(guest2);
          newAdjacents[guest2].push(guest1);
        }
      }
      
      newState = {
        ...state,
        constraints: newConstraints,
        adjacents: newAdjacents,
        seatingPlans: []
      };
      break;
    }
    
    case 'REMOVE_ADJACENT': {
      const { guest1, guest2 } = action.payload;
      const newAdjacents = { ...state.adjacents };
      
      if (newAdjacents[guest1]) {
        newAdjacents[guest1] = newAdjacents[guest1].filter(g => g !== guest2);
      }
      if (newAdjacents[guest2]) {
        newAdjacents[guest2] = newAdjacents[guest2].filter(g => g !== guest1);
      }
      
      newState = {
        ...state,
        adjacents: newAdjacents,
        seatingPlans: []
      };
      break;
    }
    
    case 'ADD_TABLE': {
      const newTables = [...state.tables];
      const newId = newTables.length ? Math.max(...newTables.map(t => t.id)) + 1 : 1;
      newTables.push({
        id: newId,
        seats: action.payload?.seats || 8,
        ...action.payload
      });
      
      newState = {
        ...state,
        tables: newTables,
        seatingPlans: [],
        userSetTables: true
      };
      break;
    }
    
    case 'REMOVE_TABLE': {
      const newTables = state.tables.filter(t => t.id !== action.payload);
      
      const newAssignments = { ...state.assignments };
      Object.keys(newAssignments).forEach(guest => {
        const tables = newAssignments[guest].split(',').map(t => t.trim());
        const filteredTables = tables.filter(t => t !== action.payload.toString());
        if (filteredTables.length === 0) {
          delete newAssignments[guest];
        } else {
          newAssignments[guest] = filteredTables.join(',');
        }
      });
      
      newState = {
        ...state,
        tables: newTables,
        assignments: newAssignments,
        seatingPlans: [],
        userSetTables: true
      };
      break;
    }
    
    case 'UPDATE_TABLE_SEATS': {
      const newTables = state.tables.map(table => 
        table.id === action.payload.id 
          ? { ...table, seats: action.payload.seats } 
          : table
      );
      
      newState = {
        ...state,
        tables: newTables,
        seatingPlans: [],
        userSetTables: true
      };
      break;
    }
    
    case 'UPDATE_TABLE_NAME': {
      const newTables = state.tables.map(table => 
        table.id === action.payload.id 
          ? { ...table, name: action.payload.name } 
          : table
      );
      
      newState = {
        ...state,
        tables: newTables,
        seatingPlans: []
      };
      break;
    }
    
    case 'UPDATE_ASSIGNMENT': {
      const newAssignments = { ...state.assignments };
      if (action.payload.tables) {
        newAssignments[action.payload.name] = action.payload.tables;
      } else {
        delete newAssignments[action.payload.name];
      }
      
      newState = {
        ...state,
        assignments: newAssignments,
        seatingPlans: []
      };
      break;
    }
    
    case 'SET_SEATING_PLANS':
      newState = {
        ...state,
        seatingPlans: action.payload,
        currentPlanIndex: 0
      };
      break;
    
    case 'SET_CURRENT_PLAN_INDEX':
      newState = {
        ...state,
        currentPlanIndex: action.payload
      };
      break;
    
    case 'SET_SUBSCRIPTION':
      console.log('Setting subscription in app context:', action.payload);
      newState = {
        ...state,
        subscription: action.payload
      };
      break;
    
    case 'SET_USER':
      console.log('Setting user in app context:', action.payload);
      newState = {
        ...state,
        user: action.payload
      };
      break;
    
    case 'SET_USER_SET_TABLES':
      newState = {
        ...state,
        userSetTables: action.payload
      };
      break;
    
    case 'UPDATE_DEFAULT_TABLES':
      newState = {
        ...state,
        tables: action.payload,
        seatingPlans: []
      };
      break;
    
    case 'SET_LOADED_SAVED_SETTING':
      newState = {
        ...state,
        loadedSavedSetting: action.payload
      };
      break;
    
    case 'IMPORT_STATE': {
      // Ensure we have all the required fields with proper defaults
      const importedState = { 
        ...action.payload,
        // Default properties if they don't exist
        guests: action.payload.guests || [],
        tables: action.payload.tables || defaultTables,
        constraints: action.payload.constraints || {},
        adjacents: action.payload.adjacents || {},
        assignments: action.payload.assignments || {},
        seatingPlans: action.payload.seatingPlans || [],
        currentPlanIndex: action.payload.currentPlanIndex || 0,
        userSetTables: action.payload.userSetTables !== undefined ? action.payload.userSetTables : true
      };
      
      // Ensure tables have the correct structure
      if (importedState.tables) {
        importedState.tables = importedState.tables.map(table => {
          if (!table.hasOwnProperty('seats')) {
            return { 
              ...table, 
              seats: 8 // Default value if missing
            };
          }
          return table;
        });
      }
      
      console.log("Importing state with tables:", importedState.tables);
      
      newState = {
        ...state,
        ...importedState,
        // Keep the current subscription and user state
        subscription: state.subscription,
        user: state.user,
        loadedSavedSetting: true // Mark that we've loaded a saved setting
      };
      break;
    }
    
    case 'RESET':
      newState = {
        ...initialState,
        // Keep the current subscription and user state
        subscription: state.subscription,
        user: state.user,
        loadedSavedSetting: false
      };
      
      // Clear recent session settings for premium users
      if (state.user && isPremiumSubscription(state.subscription)) {
        clearRecentSessionSettings(state.user.id, true);
      }
      
      break;
    
    default:
      return state;
  }

  try {
    // Save to localStorage but exclude the subscription and user
    const stateToSave = { ...newState };
    delete stateToSave.subscription;
    delete stateToSave.user;
    
    localStorage.setItem(STORAGE_KEY, JSON.stringify(stateToSave));
  } catch (error) {
    console.error('Error saving state:', error);
  }

  return newState;
};

interface AppContextType {
  state: AppState;
  dispatch: React.Dispatch<AppAction>;
}

const AppContext = createContext<AppContextType | undefined>(undefined);

export const AppProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, loadSavedState());
  
  // Check for user and fetch user info
  useEffect(() => {
    const fetchUserData = async () => {
      if (!supabaseConfigured) {
        console.log('Supabase not configured - skipping user check');
        return;
      }
      
      try {
        const { data } = await supabase.auth.getUser();
        if (data?.user) {
          console.log('User found:', data.user.id);
          dispatch({ type: 'SET_USER', payload: data.user });
        }
      } catch (error) {
        console.error('Error fetching user data:', error);
      }
    };
    
    fetchUserData();
    
    // Subscribe to auth changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange((event, session) => {
      console.log('Auth state changed:', event);
      if (event === 'SIGNED_IN' && session?.user) {
        dispatch({ type: 'SET_USER', payload: session.user });
      } else if (event === 'SIGNED_OUT') {
        dispatch({ type: 'SET_USER', payload: null });
        dispatch({ type: 'SET_SUBSCRIPTION', payload: null });
      }
    });
    
    return () => subscription.unsubscribe();
  }, []);
  
  // Check for user and fetch subscription status if Supabase is configured
  useEffect(() => {
    const fetchUserSubscription = async () => {
      if (!supabaseConfigured) {
        console.log('Supabase not configured - skipping subscription check');
        return;
      }
      
      try {
        const { data: { user } } = await supabase.auth.getUser();
        if (user) {
          console.log('Fetching subscription for user:', user.id);
          
          // First check for regular subscription
          const { data, error } = await supabase
            .from('subscriptions')
            .select('*')
            .eq('user_id', user.id)
            .order('current_period_end', { ascending: false })
            .limit(1);
            
          if (!error && data?.length > 0) {
            console.log('Found subscription:', data[0]);
            dispatch({ type: 'SET_SUBSCRIPTION', payload: data[0] });
            return;
          } else if (error) {
            console.error('Error fetching subscription:', error);
          }
          
          // If no regular subscription, check for trial subscription
          const { data: trialData, error: trialError } = await supabase
            .from('trial_subscriptions')
            .select('*')
            .eq('user_id', user.id)
            .gt('expires_on', new Date().toISOString())
            .limit(1);
            
          if (!trialError && trialData?.length > 0) {
            console.log('Found trial subscription:', trialData[0]);
            // Create a virtual subscription object from the trial
            const virtualSubscription = {
              id: `trial-${trialData[0].id}`,
              user_id: user.id,
              status: 'active',
              current_period_end: trialData[0].expires_on,
              cancel_at_period_end: true,
              trial_end: trialData[0].expires_on
            };
            dispatch({ type: 'SET_SUBSCRIPTION', payload: virtualSubscription });
          } else if (trialError) {
            console.error('Error fetching trial subscription:', trialError);
          }
        }
      } catch (error) {
        console.error('Error fetching user subscription:', error);
      }
    };
    
    fetchUserSubscription();
  }, []);

  // Load recent session settings when user logs in
  useEffect(() => {
    const loadRecentSession = async () => {
      if (state.user && isPremiumSubscription(state.subscription) && !state.loadedSavedSetting) {
        console.log('Attempting to load recent session settings for premium user');
        
        try {
          const recentTables = await loadRecentSessionSettings(state.user.id, true);
          if (recentTables && recentTables.length > 0) {
            console.log('Loaded recent session table settings:', recentTables.length);
            dispatch({ type: 'UPDATE_DEFAULT_TABLES', payload: recentTables });
            dispatch({ type: 'SET_USER_SET_TABLES', payload: true });
          }
        } catch (error) {
          console.error('Error loading recent session settings:', error);
        }
      }
    };
    
    loadRecentSession();
  }, [state.user, state.subscription, state.loadedSavedSetting]);

  useEffect(() => {
    if (!state.userSetTables && state.guests.length > 0) {
      const totalGuests = state.guests.reduce((sum, guest) => sum + guest.count, 0);
      const requiredTables = calculateRequiredTables(totalGuests);
      
      if (JSON.stringify(requiredTables) !== JSON.stringify(state.tables)) {
        dispatch({ type: 'UPDATE_DEFAULT_TABLES', payload: requiredTables });
      }
    }
  }, [state.guests, state.userSetTables]);
  
  return (
    <AppContext.Provider value={{ state, dispatch }}>
      {children}
    </AppContext.Provider>
  );
};

export const useApp = () => {
  const context = useContext(AppContext);
  if (context === undefined) {
    throw new Error('useApp must be used within an AppProvider');
  }
  return context;
};