import React, { useState, useEffect } from 'react';
import { Save, FolderOpen, Edit2, Copy, Trash2, AlertCircle, Crown } from 'lucide-react';
import Card from '../components/Card';
import Button from '../components/Button';
import { useApp } from '../context/AppContext';
import { supabase } from '../lib/supabase';
import { isPremiumSubscription, getMaxSavedSettingsLimit, canSaveMoreSettings } from '../utils/premium';
import AuthModal from '../components/AuthModal';
import { useNavigate } from 'react-router-dom';
import { clearRecentSessionSettings } from '../lib/sessionSettings';

interface SavedSetting {
  id: string;
  name: string;
  data: any;
  created_at: string;
  updated_at: string;
}

const SavedSettings: React.FC = () => {
  const { state, dispatch } = useApp();
  const [settings, setSettings] = useState<SavedSetting[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showSaveModal, setShowSaveModal] = useState(false);
  const [newSettingName, setNewSettingName] = useState('');
  const [showAuthModal, setShowAuthModal] = useState(false);
  const [savingSettings, setSavingSettings] = useState(false);
  const navigate = useNavigate();

  // Get user and subscription from global app state
  const { user, subscription } = state;

  useEffect(() => {
    if (user) {
      loadSettings();
    } else {
      setSettings([]);
      setLoading(false);
    }
  }, [user]);

  const loadSettings = async () => {
    try {
      if (!user) {
        setSettings([]);
        setLoading(false);
        return;
      }
      
      setLoading(true);
      setError(null);
      
      const { data, error } = await supabase
        .from('saved_settings')
        .select('*')
        .order('updated_at', { ascending: false });

      if (error) {
        console.error('Error loading settings:', error);
        throw error;
      }
      
      console.log('Loaded settings:', data?.length || 0);
      setSettings(data || []);
    } catch (err) {
      setError('Failed to load saved settings');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const handleSave = async () => {
    if (!user) {
      setShowAuthModal(true);
      return;
    }
    
    if (!newSettingName.trim()) {
      setError('Please enter a name for your settings');
      return;
    }

    try {
      // Check if user is premium
      const isPremium = isPremiumSubscription(subscription);
      const maxSettings = getMaxSavedSettingsLimit(isPremium ? { status: 'active' } : null);
      
      // Check if user has reached their limit
      if (settings.length >= maxSettings && !isPremium) {
        setError(`Free users can only save up to ${maxSettings} settings. Upgrade to Premium for unlimited settings.`);
        return;
      }

      // Ensure we capture the full state including tables and their seats
      const settingData = {
        version: "1.0",
        guests: state.guests,
        tables: state.tables.map(table => ({
          id: table.id,
          seats: table.seats,
          name: table.name
        })),
        constraints: state.constraints,
        adjacents: state.adjacents,
        assignments: state.assignments,
        seatingPlans: state.seatingPlans,
        currentPlanIndex: state.currentPlanIndex,
        userSetTables: state.userSetTables
      };

      const { error } = await supabase
        .from('saved_settings')
        .insert({
          name: newSettingName,
          data: settingData
        });

      if (error) {
        console.error('Error saving settings:', error);
        if (error.message.includes('violates row-level security policy')) {
          throw new Error('You don\'t have permission to save settings. Please make sure you\'re logged in.');
        } else {
          throw error;
        }
      }

      setShowSaveModal(false);
      setNewSettingName('');
      loadSettings();
    } catch (err) {
      console.error('Failed to save settings:', err);
      setError(err.message || 'Failed to save settings');
    }
  };

  const handleLoad = async (setting: SavedSetting) => {
    try {
      console.log("Loading saved setting:", setting);
      
      // Ensure the data object is complete
      if (!setting.data) {
        throw new Error('Saved setting data is missing or corrupted');
      }
      
      // Check if all required properties exist
      const requiredProps = ['guests', 'tables', 'constraints', 'adjacents', 'assignments'];
      const missingProps = requiredProps.filter(prop => !setting.data.hasOwnProperty(prop));
      
      if (missingProps.length > 0) {
        console.warn(`Missing properties in saved settings: ${missingProps.join(', ')}`);
        // Fill in missing properties with defaults
        missingProps.forEach(prop => {
          switch (prop) {
            case 'guests':
              setting.data.guests = [];
              break;
            case 'tables':
              setting.data.tables = [];
              break;
            case 'constraints':
              setting.data.constraints = {};
              break;
            case 'adjacents':
              setting.data.adjacents = {};
              break;
            case 'assignments':
              setting.data.assignments = {};
              break;
            default:
              break;
          }
        });
      }
      
      // Ensure tables have the proper structure
      if (setting.data.tables) {
        setting.data.tables = setting.data.tables.map(table => {
          // If table is missing seats property, add default value
          if (!table.hasOwnProperty('seats')) {
            return {
              ...table,
              seats: 8 // Default value
            };
          }
          return table;
        });
      }
      
      // Add userSetTables flag if missing
      if (!setting.data.hasOwnProperty('userSetTables')) {
        setting.data.userSetTables = true; // Default to true for saved settings
      }
      
      // Clear any recent session settings since we're loading an explicit saved setting
      if (isPremiumSubscription(state.subscription) && user) {
        await clearRecentSessionSettings(user.id, true);
      }
      
      // Import the data into state
      dispatch({ type: 'IMPORT_STATE', payload: setting.data });
      dispatch({ type: 'SET_LOADED_SAVED_SETTING', payload: true });
      
      // Update the setting's last_accessed timestamp
      await supabase
        .from('saved_settings')
        .update({ updated_at: new Date().toISOString() })
        .eq('id', setting.id);
      
      // Navigate to the Seating tab page
      navigate('/seating');
    } catch (err) {
      console.error('Failed to load settings:', err);
      setError('Failed to load settings: ' + (err.message || 'Unknown error'));
    }
  };

  const handleDuplicate = async (setting: SavedSetting) => {
    try {
      // Check if user is premium
      const isPremium = isPremiumSubscription(subscription);
      const maxSettings = getMaxSavedSettingsLimit(isPremium ? { status: 'active' } : null);
      
      // Check if user has reached their limit
      if (settings.length >= maxSettings && !isPremium) {
        setError(`Free users can only save up to ${maxSettings} settings. Upgrade to Premium for unlimited settings.`);
        return;
      }
      
      const { error } = await supabase
        .from('saved_settings')
        .insert({
          name: `${setting.name} (Copy)`,
          data: setting.data
        });

      if (error) throw error;
      loadSettings();
    } catch (err) {
      setError('Failed to duplicate settings');
      console.error(err);
    }
  };

  const handleDelete = async (id: string) => {
    if (!window.confirm('Are you sure you want to delete these settings?')) return;

    try {
      const { error } = await supabase
        .from('saved_settings')
        .delete()
        .eq('id', id);

      if (error) throw error;
      loadSettings();
    } catch (err) {
      setError('Failed to delete settings');
      console.error(err);
    }
  };

  const handleRename = async (id: string, currentName: string) => {
    const newName = prompt('Enter new name:', currentName);
    if (!newName || newName === currentName) return;

    try {
      const { error } = await supabase
        .from('saved_settings')
        .update({ name: newName })
        .eq('id', id);

      if (error) throw error;
      loadSettings();
    } catch (err) {
      setError('Failed to rename settings');
      console.error(err);
    }
  };

  // Check premium status from global state
  const isPremium = isPremiumSubscription(subscription);
  const maxSettings = getMaxSavedSettingsLimit(isPremium ? { status: 'active' } : null);

  if (!user) {
    return (
      <div className="space-y-6">
        <h1 className="text-2xl font-bold text-[#7973BB] flex items-center">
          <FolderOpen className="mr-2" />
          Saved Settings
        </h1>

        <Card>
          <div className="text-center py-8">
            <p className="text-gray-600 mb-4">
              You need to be logged in to access saved settings.
            </p>
            <Button onClick={() => setShowAuthModal(true)}>
              Log In or Sign Up
            </Button>
          </div>
        </Card>
        
        {showAuthModal && <AuthModal onClose={() => setShowAuthModal(false)} />}
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-bold text-[#7973BB] flex items-center">
        <FolderOpen className="mr-2" />
        Saved Settings
        {isPremium && (
          <span className="flex items-center bg-yellow-500 text-black px-2 py-0.5 rounded-full text-sm font-medium ml-2">
            <Crown className="w-4 h-4 mr-1" />
            Premium
          </span>
        )}
      </h1>

      <Card>
        <div className="space-y-4">
          <p className="text-gray-700">
            Save and manage your seating arrangements.
            {!isPremium 
              ? ` Free users can save up to ${maxSettings} configurations. Premium users can save up to 50.`
              : ` Premium users can save up to 50 configurations.`}
          </p>

          <Button
            onClick={() => setShowSaveModal(true)}
            icon={<Save className="w-4 h-4" />}
            disabled={isPremium ? settings.length >= 50 : settings.length >= maxSettings}
          >
            Save Current Settings
          </Button>

          {error && (
            <div className="bg-red-50 border border-red-200 rounded-md p-3 flex items-start">
              <AlertCircle className="w-5 h-5 text-red-500 mr-2 flex-shrink-0 mt-0.5" />
              <p className="text-red-700">{error}</p>
            </div>
          )}
          
          {!isPremium && settings.length >= maxSettings && (
            <div className="bg-indigo-50 border border-indigo-200 rounded-md p-3">
              <p className="text-indigo-700">
                You've reached your limit of {maxSettings} saved settings. 
                <span className="font-bold"> Upgrade to Premium for more saved settings!</span>
              </p>
            </div>
          )}
          
          {isPremium && (
            <div className="bg-green-50 border border-green-300 rounded-md p-3">
              <p className="text-green-700 flex items-center">
                <Crown className="w-4 h-4 mr-1 text-yellow-500" />
                <span>Premium users: Your table names will automatically be preserved between sessions.</span>
              </p>
            </div>
          )}
        </div>
      </Card>

      <Card title="Saved Configurations">
        {loading ? (
          <div className="text-center py-4">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-indigo-500 mx-auto"></div>
            <p className="mt-2 text-gray-600">Loading saved settings...</p>
          </div>
        ) : settings.length === 0 ? (
          <div className="text-center py-4 text-gray-500">
            No saved settings yet. Click "Save Current Settings" to create your first configuration.
          </div>
        ) : (
          <div className="space-y-4">
            {settings.map((setting) => (
              <div
                key={setting.id}
                className="border border-gray-200 rounded-lg p-4 hover:bg-gray-50 transition-colors cursor-pointer"
                onClick={() => handleLoad(setting)}
              >
                <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
                  <div>
                    <h3 className="font-medium text-gray-900">{setting.name}</h3>
                    <p className="text-sm text-gray-500">
                      Last modified: {new Date(setting.updated_at).toLocaleDateString()}
                    </p>
                    {setting.data?.guests && (
                      <p className="text-sm text-gray-600">
                        {setting.data.guests.length} guests • {setting.data.tables?.length || 0} tables
                      </p>
                    )}
                  </div>
                  <div className="flex flex-wrap gap-2">
                    <Button
                      variant="secondary"
                      size="sm"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleLoad(setting);
                      }}
                      icon={<FolderOpen className="w-4 h-4" />}
                      className="bg-green-100 hover:bg-green-200 border border-green-300"
                    >
                      Load
                    </Button>
                    <Button
                      variant="secondary"
                      size="sm"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleRename(setting.id, setting.name);
                      }}
                      icon={<Edit2 className="w-4 h-4" />}
                    >
                      Rename
                    </Button>
                    <Button
                      variant="secondary"
                      size="sm"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleDuplicate(setting);
                      }}
                      icon={<Copy className="w-4 h-4" />}
                      disabled={!isPremium && settings.length >= maxSettings}
                    >
                      Duplicate
                    </Button>
                    <Button
                      variant="danger"
                      size="sm"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleDelete(setting.id);
                      }}
                      icon={<Trash2 className="w-4 h-4" />}
                    >
                      Delete
                    </Button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </Card>

      {showSaveModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 max-w-md w-full shadow-2xl">
            <h3 className="text-xl font-bold mb-4">Save Current Settings</h3>
            <div className="mb-4">
              <label htmlFor="settingName" className="block text-sm font-medium text-gray-700 mb-1">
                Configuration Name
              </label>
              <input
                id="settingName"
                type="text"
                value={newSettingName}
                onChange={(e) => setNewSettingName(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#7973BB]"
                placeholder="Enter a name for these settings"
              />
            </div>
            <div className="flex justify-end space-x-2">
              <Button
                variant="secondary"
                onClick={() => setShowSaveModal(false)}
              >
                Cancel
              </Button>
              <Button
                onClick={handleSave}
                icon={<Save className="w-4 h-4" />}
              >
                Save Settings
              </Button>
            </div>
          </div>
        </div>
      )}
      
      {showAuthModal && <AuthModal onClose={() => setShowAuthModal(false)} />}
    </div>
  );
};

export default SavedSettings;