import { Guest, Table, SeatingPlan, TableAssignment, ValidationError } from '../types';

// Helper function to shuffle an array
function shuffleArray<T>(array: T[]): T[] {
  const result = [...array];
  for (let i = result.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [result[i], result[j]] = [result[j], result[i]];
  }
  return result;
}

// Validates constraints and assignments for consistency
function validateConstraints(
  guests: Guest[],
  tables: Table[],
  constraints: Record<string, Record<string, 'must' | 'cannot' | ''>>,
  assignments: Record<string, string>
): ValidationError[] {
  const errors: ValidationError[] = [];
  
  // Check that assignments reference valid tables
  Object.keys(assignments).forEach(guestName => {
    const tablesAssigned = assignments[guestName].split(',').map(t => parseInt(t.trim())).filter(t => !isNaN(t));
    tablesAssigned.forEach(tableId => {
      if (!tables.some(table => table.id === tableId)) {
        errors.push({
          message: `Invalid table assignment for ${guestName}: Table ${tableId} does not exist.`,
          type: 'error'
        });
      }
    });
  });
  
  // Check that 'must' constraints don't conflict with assignments
  Object.keys(constraints).forEach(guest1 => {
    Object.keys(constraints[guest1] || {}).forEach(guest2 => {
      if (constraints[guest1][guest2] === 'must' && assignments[guest1] && assignments[guest2]) {
        const tables1 = assignments[guest1].split(',').map(t => parseInt(t.trim())).filter(t => !isNaN(t));
        const tables2 = assignments[guest2].split(',').map(t => parseInt(t.trim())).filter(t => !isNaN(t));
        
        if (tables1.length > 0 && tables2.length > 0 && !tables1.some(t => tables2.includes(t))) {
          errors.push({
            message: `Constraint conflict: ${guest1} and ${guest2} must be at the same table but are assigned to non-overlapping tables.`,
            type: 'error'
          });
        }
      }
    });
  });
  
  // Check that there are enough seats for all guests
  const totalGuests = guests.reduce((sum, guest) => sum + guest.count, 0);
  const totalSeats = tables.reduce((sum, table) => sum + table.seats, 0);
  
  if (totalGuests > totalSeats) {
    errors.push({
      message: `Not enough seats (${totalSeats}) for all guests (${totalGuests}). Add more tables or seats.`,
      type: 'error'
    });
  }
  
  return errors;
}

// Find groups of guests that must sit together
function buildMustGroups(
  guests: Guest[],
  constraints: Record<string, Record<string, 'must' | 'cannot' | ''>>
): string[][] {
  const mustGroups: string[][] = [];
  const visited = new Set<string>();
  
  guests.forEach(guest => {
    const guestName = guest.name;
    
    if (!visited.has(guestName)) {
      const group: string[] = [guestName];
      const queue: string[] = [guestName];
      visited.add(guestName);
      
      while (queue.length > 0) {
        const current = queue.shift()!;
        
        Object.keys(constraints[current] || {}).forEach(otherGuest => {
          if (constraints[current][otherGuest] === 'must' && !visited.has(otherGuest)) {
            group.push(otherGuest);
            queue.push(otherGuest);
            visited.add(otherGuest);
          }
        });
      }
      
      if (group.length > 1) {
        mustGroups.push(group);
      }
    }
  });
  
  return mustGroups;
}

// Check if a guest can be assigned to a table based on constraints
function canAssignToTable(
  guest: Guest,
  tableGuests: Guest[],
  constraints: Record<string, Record<string, 'must' | 'cannot' | ''>>
): boolean {
  for (const tableGuest of tableGuests) {
    if (constraints[guest.name]?.[tableGuest.name] === 'cannot') {
      return false;
    }
  }
  return true;
}

// Calculate total people count for a table
function getTablePeopleCount(tableGuests: Guest[]): number {
  return tableGuests.reduce((sum, guest) => sum + guest.count, 0);
}

// Generate a single seating plan
function generatePlan(
  guests: Guest[],
  tables: Table[],
  constraints: Record<string, Record<string, 'must' | 'cannot' | ''>>,
  adjacents: Record<string, string[]>,
  assignments: Record<string, string>
): SeatingPlan | null {
  // Initialize plan with empty tables
  const plan: TableAssignment[] = tables.map(table => ({
    id: table.id,
    seats: [],
    capacity: table.seats
  }));
  
  // Copy guests to avoid modifying the original
  const availableGuests = [...guests];
  
  // First, place guests with 'must' constraints together
  const mustGroups = buildMustGroups(guests, constraints);
  
  for (const group of mustGroups) {
    const groupGuests = group
      .map(name => guests.find(g => g.name === name))
      .filter((g): g is Guest => g !== undefined && availableGuests.some(ag => ag.name === g.name));
    
    if (groupGuests.length > 1) {
      // Calculate total people count for the group (not just guest units)
      const totalCount = groupGuests.reduce((sum, g) => sum + g.count, 0);
      
      // Find tables that all members of the group are allowed to be assigned to
      let allowedTables = plan;
      
      const hasAssignments = groupGuests.some(guest => assignments[guest.name]);
      if (hasAssignments) {
        allowedTables = plan.filter(table => {
          return groupGuests.every(guest => {
            if (!assignments[guest.name]) return true;
            const assignedTableIds = assignments[guest.name].split(',').map(Number);
            return assignedTableIds.includes(table.id);
          });
        });
      }
      
      // Find a table with enough capacity
      const table = allowedTables.find(t => {
        const currentPeopleCount = getTablePeopleCount(t.seats);
        return currentPeopleCount + totalCount <= t.capacity;
      });
      
      if (!table) {
        // If no table has enough capacity, we can't create a valid plan
        return null;
      }
      
      // Assign all guests in the group to the table
      for (const guest of groupGuests) {
        table.seats.push(guest);
        const index = availableGuests.findIndex(g => g.name === guest.name);
        if (index !== -1) {
          availableGuests.splice(index, 1);
        }
      }
    }
  }
  
  // Next, place guests with specific table assignments
  for (const guestName of Object.keys(assignments)) {
    const guest = availableGuests.find(g => g.name === guestName);
    if (!guest) continue; // Guest already placed as part of a group
    
    const assignedTableIds = assignments[guestName].split(',').map(t => parseInt(t.trim())).filter(t => !isNaN(t));
    
    let assigned = false;
    for (const tableId of assignedTableIds) {
      const table = plan.find(t => t.id === tableId);
      if (table) {
        // Check if there's enough capacity for this guest
        const currentPeopleCount = getTablePeopleCount(table.seats);
        
        // Only assign if adding this guest won't exceed capacity
        if (currentPeopleCount + guest.count <= table.capacity && 
            canAssignToTable(guest, table.seats, constraints)) {
          table.seats.push(guest);
          const index = availableGuests.findIndex(g => g.name === guestName);
          if (index !== -1) {
            availableGuests.splice(index, 1);
          }
          assigned = true;
          break;
        }
      }
    }
    
    if (!assigned && assignedTableIds.length > 0) {
      // If we couldn't assign a guest to any of their specified tables, the plan is invalid
      return null;
    }
  }
  
  // Finally, place remaining guests using backtracking
  if (!backtrackAssign(plan, availableGuests, 0, constraints)) {
    return null;
  }
  
  // Attempt to arrange adjacencies
  arrangeAdjacencies(plan, adjacents);
  
  return {
    id: Math.floor(Math.random() * 1000000), // Unique ID for the plan
    tables: plan
  };
}

// Backtracking algorithm to assign remaining guests
function backtrackAssign(
  plan: TableAssignment[],
  availableGuests: Guest[],
  index: number,
  constraints: Record<string, Record<string, 'must' | 'cannot' | ''>>
): boolean {
  if (index >= availableGuests.length) {
    return true; // All guests have been assigned
  }
  
  const guest = availableGuests[index];
  const shuffledTables = shuffleArray(plan);
  
  for (const table of shuffledTables) {
    // Check if adding this guest would exceed the table's capacity
    const currentPeopleCount = getTablePeopleCount(table.seats);
    
    if (currentPeopleCount + guest.count <= table.capacity && 
        canAssignToTable(guest, table.seats, constraints)) {
      
      table.seats.push(guest);
      
      if (backtrackAssign(plan, availableGuests, index + 1, constraints)) {
        return true;
      }
      
      // Backtrack if we couldn't assign the remaining guests
      table.seats.pop();
    }
  }
  
  return false; // Couldn't assign this guest
}

// Rearrange guests at tables to satisfy adjacency constraints
function arrangeAdjacencies(
  plan: TableAssignment[],
  adjacents: Record<string, string[]>
): void {
  for (const table of plan) {
    if (table.seats.length <= 1) continue;
    
    // Build adjacency requirements for this table
    const tableAdjacencyMap: Record<string, string[]> = {};
    for (const guest of table.seats) {
      if (adjacents[guest.name]) {
        tableAdjacencyMap[guest.name] = adjacents[guest.name].filter(adjName => 
          table.seats.some(tableGuest => tableGuest.name === adjName)
        );
      }
    }
    
    // If there are no adjacency requirements for this table, continue
    if (Object.keys(tableAdjacencyMap).length === 0) continue;
    
    // Create a new ordering for guests
    const orderedSeats: Guest[] = [];
    const placed = new Set<string>();
    
    // Helper function to place a guest and its adjacents
    const placeGuest = (guest: Guest): void => {
      if (placed.has(guest.name)) return;
      
      placed.add(guest.name);
      orderedSeats.push(guest);
      
      // Try to place adjacent guests next
      const adjacentNames = tableAdjacencyMap[guest.name] || [];
      for (const adjName of adjacentNames) {
        if (!placed.has(adjName)) {
          const adjacentGuest = table.seats.find(g => g.name === adjName);
          if (adjacentGuest) {
            placeGuest(adjacentGuest);
          }
        }
      }
    };
    
    // Start with a guest that has adjacency requirements
    const startingGuests = table.seats.filter(g => tableAdjacencyMap[g.name]?.length > 0);
    if (startingGuests.length > 0) {
      placeGuest(startingGuests[0]);
    }
    
    // Place any remaining guests
    for (const guest of table.seats) {
      if (!placed.has(guest.name)) {
        placeGuest(guest);
      }
    }
    
    // Update the table's seat ordering
    if (orderedSeats.length === table.seats.length) {
      table.seats = orderedSeats;
    }
  }
}

// Check if two seating plans are similar
function plansAreSimilar(plan1: SeatingPlan, plan2: SeatingPlan): boolean {
  if (plan1.tables.length !== plan2.tables.length) {
    return false;
  }
  
  return plan1.tables.every((table1, index) => {
    const table2 = plan2.tables[index];
    if (table1.seats.length !== table2.seats.length) {
      return false;
    }
    
    const guests1 = table1.seats.map(g => g.name).sort().join(',');
    const guests2 = table2.seats.map(g => g.name).sort().join(',');
    return guests1 === guests2;
  });
}

// Main function to generate multiple seating plans
export async function generateSeatingPlans(
  guests: Guest[],
  tables: Table[],
  constraints: Record<string, Record<string, 'must' | 'cannot' | ''>>,
  adjacents: Record<string, string[]>,
  assignments: Record<string, string>,
  isPremium: boolean = false
): Promise<{ plans: SeatingPlan[], errors: ValidationError[] }> {
  const errors = validateConstraints(guests, tables, constraints, assignments);
  
  // If there are critical errors, return early
  if (errors.some(error => error.type === 'error')) {
    return { plans: [], errors };
  }
  
  const plans: SeatingPlan[] = [];
  const maxPlans = isPremium ? 30 : 10; // Premium users get 30 plans, free users get 10
  const maxAttempts = isPremium ? 3000 : 1000; // More attempts for premium users
  let attempts = 0;
  
  // To improve UX, we'll use a timeout to give the UI a chance to update
  await new Promise(resolve => setTimeout(resolve, 0));
  
  const startTime = Date.now();
  const timeLimit = isPremium ? 30000 : 10000; // 30 seconds for premium, 10 seconds for free
  
  while (plans.length < maxPlans && attempts < maxAttempts && Date.now() - startTime < timeLimit) {
    attempts++;
    
    // Every 100 attempts, give the UI a chance to update
    if (attempts % 100 === 0) {
      await new Promise(resolve => setTimeout(resolve, 0));
    }
    
    const plan = generatePlan(guests, tables, constraints, adjacents, assignments);
    
    if (plan && !plans.some(existingPlan => plansAreSimilar(existingPlan, plan))) {
      plans.push(plan);
    }
  }
  
  if (plans.length === 0 && attempts >= maxAttempts) {
    errors.push({
      message: 'Unable to generate a valid seating plan. Try relaxing some constraints or adding more tables.',
      type: 'error'
    });
  } else if (plans.length < maxPlans && attempts >= maxAttempts) {
    errors.push({
      message: `Only able to generate ${plans.length} different seating plans. Try relaxing some constraints for more options.`,
      type: 'warning'
    });
  }
  
  // Check if any plans had table capacity issues
  let hasCapacityIssues = false;
  plans.forEach(plan => {
    plan.tables.forEach(table => {
      const totalAssigned = getTablePeopleCount(table.seats);
      if (totalAssigned > table.capacity) {
        hasCapacityIssues = true;
      }
    });
  });
  
  if (hasCapacityIssues) {
    errors.push({
      message: 'One or more tables were overfilled during plan generation.',
      type: 'error'
    });
  }
  
  return { plans, errors };
}