import { supabase } from './supabase';

/**
 * Saves the user's recent table settings to Supabase (for premium users)
 * or localStorage (for non-premium users)
 */
export const saveRecentSessionSettings = async (
  userId: string | undefined,
  isPremium: boolean,
  tables: any[]
) => {
  if (!tables || tables.length === 0) return;

  try {
    // Always save to localStorage for quick access during the session
    localStorage.setItem('seatyr_recent_tables', JSON.stringify(tables));

    // Only save to Supabase for premium users
    if (isPremium && userId) {
      // First, check if this user already has a recent session record
      const { data, error: fetchError } = await supabase
        .from('recent_session_settings')
        .select('id')
        .eq('user_id', userId)
        .limit(1);

      if (fetchError) {
        console.error('Error fetching recent session settings:', fetchError);
        return;
      }

      const tableData = {
        tables: tables,
        timestamp: new Date().toISOString()
      };

      if (data && data.length > 0) {
        // Update existing record
        const { error: updateError } = await supabase
          .from('recent_session_settings')
          .update({
            data: tableData,
            updated_at: new Date().toISOString()
          })
          .eq('id', data[0].id);

        if (updateError) {
          console.error('Error updating recent session settings:', updateError);
        }
      } else {
        // Insert new record
        const { error: insertError } = await supabase
          .from('recent_session_settings')
          .insert({
            user_id: userId,
            data: tableData
          });

        if (insertError) {
          console.error('Error inserting recent session settings:', insertError);
        }
      }
    }
  } catch (error) {
    console.error('Error saving recent session settings:', error);
  }
};

/**
 * Loads the user's recent table settings from Supabase (for premium users)
 * or localStorage (for non-premium users)
 */
export const loadRecentSessionSettings = async (
  userId: string | undefined,
  isPremium: boolean
): Promise<any[] | null> => {
  try {
    // First check localStorage for quick access
    const localData = localStorage.getItem('seatyr_recent_tables');
    let tables = localData ? JSON.parse(localData) : null;

    // For premium users, try to load from Supabase
    if (isPremium && userId) {
      const { data, error } = await supabase
        .from('recent_session_settings')
        .select('data')
        .eq('user_id', userId)
        .order('updated_at', { ascending: false })
        .limit(1);

      if (error) {
        console.error('Error loading recent session settings:', error);
      } else if (data && data.length > 0 && data[0]?.data?.tables) {
        // Use Supabase data if available (it's more up-to-date)
        tables = data[0].data.tables;
        
        // Update localStorage with the latest data
        localStorage.setItem('seatyr_recent_tables', JSON.stringify(tables));
      }
    }

    return tables;
  } catch (error) {
    console.error('Error loading recent session settings:', error);
    return null;
  }
};

/**
 * Clears the user's recent session settings
 */
export const clearRecentSessionSettings = async (
  userId: string | undefined,
  isPremium: boolean
) => {
  try {
    // Clear from localStorage
    localStorage.removeItem('seatyr_recent_tables');

    // Only clear from Supabase for premium users
    if (isPremium && userId) {
      const { error } = await supabase
        .from('recent_session_settings')
        .delete()
        .eq('user_id', userId);

      if (error) {
        console.error('Error clearing recent session settings:', error);
      }
    }
  } catch (error) {
    console.error('Error clearing recent session settings:', error);
  }
};