import React, { useState, useEffect } from 'react';
import { UserCircle, Crown, Receipt, CreditCard } from 'lucide-react';
import Card from '../components/Card';
import Button from '../components/Button';
import { supabase } from '../lib/supabase';
import { useApp } from '../context/AppContext';
import { useNavigate } from 'react-router-dom';
import SubscriptionCard from '../components/SubscriptionCard';
import PaymentHistoryTable from '../components/PaymentHistoryTable';
import { getCustomerPortal } from '../lib/stripe';

const Account: React.FC = () => {
  const { state, dispatch } = useApp();
  const navigate = useNavigate();
  const [user, setUser] = useState<any>(null);
  const [showPaymentHistory, setShowPaymentHistory] = useState(false);
  const [savedSettings, setSavedSettings] = useState<any[]>([]);

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setUser(session?.user ?? null);
    });

    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      setUser(session?.user ?? null);
    });

    return () => subscription.unsubscribe();
  }, []);

  useEffect(() => {
    if (user) {
      fetchSavedSettings();
    } else {
      setSavedSettings([]);
    }
  }, [user]);

  const fetchSavedSettings = async () => {
    const { data: settings } = await supabase
      .from('saved_settings')
      .select('*')
      .order('updated_at', { ascending: false });

    setSavedSettings(settings || []);
  };

  const handleLoadSettings = async (setting: any) => {
    try {
      console.log("Loading saved setting from Account page:", setting);
      
      // Ensure the data object is complete
      if (!setting.data) {
        throw new Error('Saved setting data is missing or corrupted');
      }
      
      // Check if tables have the seats property
      if (setting.data.tables) {
        setting.data.tables = setting.data.tables.map(table => {
          if (!table.hasOwnProperty('seats')) {
            return { ...table, seats: 8 };
          }
          return table;
        });
      }
      
      // Add userSetTables flag if missing
      if (!setting.data.hasOwnProperty('userSetTables')) {
        setting.data.userSetTables = true; // Default to true for saved settings
      }
      
      dispatch({ type: 'IMPORT_STATE', payload: setting.data });
      alert('Settings loaded successfully!');
      // Navigate to the Seating page
      navigate('/seating');
    } catch (error) {
      console.error('Error loading settings:', error);
      alert('Failed to load settings. Please try again.');
    }
  };

  const refreshSubscription = async () => {
    if (!user) return;
    
    try {
      console.log('Refreshing subscription data for user:', user.id);
      
      // First check for regular subscription
      const { data, error } = await supabase
        .from('subscriptions')
        .select('*')
        .eq('user_id', user.id)
        .order('current_period_end', { ascending: false })
        .limit(1);
        
      if (!error && data?.length > 0) {
        console.log('Found subscription:', data[0]);
        dispatch({ type: 'SET_SUBSCRIPTION', payload: data[0] });
        return;
      } else if (error) {
        console.error('Error fetching subscription:', error);
      }
      
      // If no regular subscription, check for trial subscription
      const { data: trialData, error: trialError } = await supabase
        .from('trial_subscriptions')
        .select('*')
        .eq('user_id', user.id)
        .gt('expires_on', new Date().toISOString())
        .order('expires_on', { ascending: false })
        .limit(1);
        
      if (!trialError && trialData?.length > 0) {
        console.log('Found trial subscription:', trialData[0]);
        // Create a virtual subscription object from the trial
        const virtualSubscription = {
          id: `trial-${trialData[0].id}`,
          user_id: user.id,
          status: 'active',
          current_period_end: trialData[0].expires_on,
          cancel_at_period_end: true,
          trial_end: trialData[0].expires_on
        };
        dispatch({ type: 'SET_SUBSCRIPTION', payload: virtualSubscription });
      } else if (trialError) {
        console.error('Error fetching trial subscription:', trialError);
      }
    } catch (error) {
      console.error('Error refreshing subscription:', error);
    }
  };

  // Function to format dates, handling Unix timestamps correctly
  const formatDate = (dateString: string | undefined) => {
    if (!dateString) return 'N/A';
    
    // Check if dateString is a Unix timestamp (seconds) and convert to milliseconds
    if (dateString && !isNaN(Number(dateString)) && dateString.length === 10) {
      return new Date(Number(dateString) * 1000).toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      });
    }
    
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  if (!user) {
    return (
      <div className="space-y-6">
        <h1 className="text-2xl font-bold text-[#7973BB] flex items-center">
          <UserCircle className="mr-2" />
          Account
        </h1>
        <Card>
          <div className="text-center py-8">
            <p className="text-gray-600 text-lg">Please log in to view your account details.</p>
          </div>
        </Card>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-bold text-[#7973BB] flex items-center">
        <UserCircle className="mr-2" />
        Account
        {state.subscription?.status === 'active' && (
          <span className="flex items-center bg-yellow-500 text-black px-2 py-0.5 rounded-full text-sm font-medium ml-2">
            <Crown className="w-4 h-4 mr-1" />
            Premium
          </span>
        )}
      </h1>

      <Card>
        <div className="space-y-4">
          <div>
            <h2 className="text-lg font-semibold text-[#7973BB]">Account Details</h2>
            <p className="text-gray-600 text-lg">{user.email}</p>
          </div>
          
          <div className="border-t pt-4">
            <h3 className="font-medium text-[#7973BB] mb-2">Account Type</h3>
            <p className="text-gray-600 text-lg">
              {state.subscription?.status === 'active' ? 
                (state.subscription.id.toString().startsWith('trial-') ? 'Beta Tester (Premium Trial)' : 'Premium Member') : 
                'Free Member'}
            </p>
          </div>
        </div>
      </Card>

      <SubscriptionCard onSuccess={refreshSubscription} />

      <Card 
        title="Payment & Billing" 
        actions={
          <Button 
            variant="secondary"
            size="sm"
            icon={showPaymentHistory ? undefined : <Receipt className="w-4 h-4" />}
            onClick={() => setShowPaymentHistory(!showPaymentHistory)}
          >
            {showPaymentHistory ? 'Hide History' : 'View Payment History'}
          </Button>
        }
      >
        <div className="space-y-4">
          <p className="text-gray-700">Manage your payment methods and view your billing history.</p>
          
          {state.subscription?.status === 'active' && !state.subscription.id.toString().startsWith('trial-') && (
            <Button
              variant="secondary"
              onClick={async () => {
                try {
                  await supabase.auth.getUser();
                  await getCustomerPortal(user.id);
                } catch (error) {
                  console.error('Error opening customer portal:', error);
                  alert('Failed to open customer portal. Please try again.');
                }
              }}
              icon={<CreditCard className="w-4 h-4" />}
            >
              Manage Payment Methods
            </Button>
          )}
          
          {showPaymentHistory && (
            <div className="mt-6">
              <h3 className="text-lg font-medium text-[#7973BB] mb-4">Payment History</h3>
              <PaymentHistoryTable userId={user.id} />
            </div>
          )}
        </div>
      </Card>

      <Card title="Saved Settings">
        {savedSettings.length === 0 ? (
          <p className="text-gray-500 text-center py-4 text-lg">No saved settings yet.</p>
        ) : (
          <div className="space-y-4">
            {savedSettings.map((setting) => (
              <div
                key={setting.id}
                className="border border-gray-200 rounded-lg p-4 hover:bg-gray-50 transition-colors cursor-pointer"
                onClick={() => handleLoadSettings(setting)}
              >
                <div>
                  <h3 className="font-medium text-[#7973BB] text-lg">{setting.name}</h3>
                  <p className="text-sm text-gray-500 text-lg">
                    Last modified: {new Date(setting.updated_at).toLocaleDateString()}
                  </p>
                  {setting.data?.guests && setting.data?.tables && (
                    <p className="text-sm text-gray-600">
                      {setting.data.guests.length} guests • {setting.data.tables.length} tables
                    </p>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </Card>
    </div>
  );
};

export default Account;