import React, { useState, useEffect } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { Users, Table, ClipboardList, MapPin, FolderOpen, Crown, UserCircle } from 'lucide-react';
import Button from './Button';
import AuthModal from './AuthModal';
import PremiumModal from './PremiumModal';
import { supabase } from '../lib/supabase';
import { redirectToCheckout } from '../lib/stripe';
import { useApp } from '../context/AppContext';
import { isPremiumSubscription } from '../utils/premium';

const Header: React.FC = () => {
  const [showAuthModal, setShowAuthModal] = useState(false);
  const [showPremiumModal, setShowPremiumModal] = useState(false);
  const [user, setUser] = useState(null);
  const [subscription, setSubscription] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();
  const { state, dispatch } = useApp();

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setUser(session?.user ?? null);
    });

    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      setUser(session?.user ?? null);
    });

    return () => subscription.unsubscribe();
  }, []);

  useEffect(() => {
    if (user) {
      fetchSubscription();
    } else {
      setSubscription(null);
      dispatch({ type: 'SET_SUBSCRIPTION', payload: null });
    }
  }, [user]);

  const fetchSubscription = async () => {
    try {
      console.log('Fetching subscription for user:', user.id);
      
      // First, try to find active subscription
      const { data, error } = await supabase
        .from('subscriptions')
        .select('*')
        .eq('user_id', user.id)
        .order('current_period_end', { ascending: false })
        .limit(1);
      
      if (error) {
        console.error('Error fetching subscription:', error);
        return;
      }
      
      const subscription = data?.[0] || null;
      console.log('Subscription data:', subscription);
      
      if (subscription) {
        setSubscription(subscription);
        dispatch({ type: 'SET_SUBSCRIPTION', payload: subscription });
      } else {
        // If no subscription found, check for active trial
        const { data: trialData, error: trialError } = await supabase
          .from('trial_subscriptions')
          .select('*')
          .eq('user_id', user.id)
          .gt('expires_on', new Date().toISOString())
          .limit(1);
          
        if (trialError) {
          console.error('Error fetching trial subscription:', trialError);
        }
        
        if (trialData && trialData.length > 0) {
          console.log('Found active trial:', trialData[0]);
          // Create a subscription object from the trial data
          const trialSubscription = {
            id: `trial-${trialData[0].id}`,
            user_id: user.id,
            status: 'active',
            current_period_start: trialData[0].start_date,
            current_period_end: trialData[0].expires_on,
            cancel_at_period_end: true
          };
          setSubscription(trialSubscription);
          dispatch({ type: 'SET_SUBSCRIPTION', payload: trialSubscription });
        } else {
          // If no subscription found, check if we should force premium for specific users
          const { data: userData } = await supabase.auth.getUser();
          const email = userData?.user?.email?.toLowerCase();
          
          console.log('User email for premium check:', email);
          
          if (email === 'danabrams999@yahoo.com' || email === 'immortality.bytes.book@gmail.com') {
            console.log('Force enabling premium for special user:', email);
            
            // Force create a subscription record for these users if missing
            await fetch(`${import.meta.env.VITE_SUPABASE_URL}/functions/v1/sync-user-premium`, {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${import.meta.env.VITE_SUPABASE_ANON_KEY}`
              },
              body: JSON.stringify({ email })
            }).catch(err => console.error('Error syncing premium status:', err));
            
            // Fetch the subscription again
            const { data: refreshedData } = await supabase
              .from('subscriptions')
              .select('*')
              .eq('user_id', user.id)
              .order('current_period_end', { ascending: false })
              .limit(1);
              
            const refreshedSubscription = refreshedData?.[0] || null;
            if (refreshedSubscription) {
              console.log('Refreshed subscription data:', refreshedSubscription);
              setSubscription(refreshedSubscription);
              dispatch({ type: 'SET_SUBSCRIPTION', payload: refreshedSubscription });
            } else {
              setSubscription(null);
              dispatch({ type: 'SET_SUBSCRIPTION', payload: null });
            }
          } else {
            setSubscription(null);
            dispatch({ type: 'SET_SUBSCRIPTION', payload: null });
          }
        }
      }
    } catch (err) {
      console.error('Error in fetchSubscription:', err);
      setSubscription(null);
      dispatch({ type: 'SET_SUBSCRIPTION', payload: null });
    }
  };

  const handleLogout = async () => {
    await supabase.auth.signOut();
    navigate('/');
  };

  const handleUpgrade = async () => {
    if (!user) {
      setShowAuthModal(true);
      return;
    }

    setShowPremiumModal(true);
  };

  const handlePremiumSuccess = () => {
    fetchSubscription();
  };

  // Use both local state and app context to determine premium status
  const isPremium = isPremiumSubscription(subscription) || isPremiumSubscription(state.subscription);

  return (
    <header className="bg-[#FEF6D1] shadow-md">
      <div className="container mx-auto px-4 py-3">
        <div className="flex flex-col space-y-3 md:space-y-2">
          {/* Logo and Title + Premium/Account Info Row */}
          <div className="flex flex-col md:flex-row md:justify-between md:items-center">
            {/* Logo and Title */}
            <div className="flex items-center">
              <img 
                src="https://i.imgur.com/RKDznxk.png"
                alt="Seatyr Logo"
                className="h-10 w-auto mr-2"
              />
              <div className="flex flex-col">
                <h1 className="text-2xl font-bold text-[#7973BB]">Seatyr</h1>
                <span className="text-xs -mt-1 text-[#7973BB]">Preview Version 0.8</span>
              </div>
            </div>
            
            {/* Premium Badge + Account Info + Sign Out */}
            <div className="flex flex-wrap items-center gap-2 mt-2 md:mt-0">
              {isPremium && (
                <span className="flex items-center bg-yellow-500 text-black px-2 py-0.5 rounded-full text-sm font-medium">
                  <Crown className="w-4 h-4 mr-1" />
                  Premium
                </span>
              )}
              
              {user ? (
                <>
                  <Button
                    variant="secondary"
                    onClick={() => navigate('/account')}
                    className="whitespace-nowrap text-[#7973BB] border-[#7973BB]"
                    icon={<UserCircle className="w-4 h-4" />}
                    size="sm"
                  >
                    <span className="text-xs truncate max-w-[140px]">{user.email}</span>
                  </Button>
                  <Button
                    variant="secondary"
                    onClick={handleLogout}
                    className="whitespace-nowrap text-[#7973BB] border-[#7973BB]"
                    size="sm"
                  >
                    Sign Out
                  </Button>
                </>
              ) : (
                <Button
                  variant="secondary"
                  onClick={() => setShowAuthModal(true)}
                  className="whitespace-nowrap text-[#7973BB] border-[#7973BB]"
                >
                  Login/Join
                </Button>
              )}
              
              {user && !isPremium && (
                <Button
                  onClick={handleUpgrade}
                  disabled={isLoading}
                  className="bg-gradient-to-r from-yellow-500 to-yellow-600 hover:from-yellow-600 hover:to-yellow-700 text-black font-medium whitespace-nowrap"
                  icon={<Crown className="w-4 h-4" />}
                  size="sm"
                >
                  {isLoading ? 'Processing...' : 'Upgrade'}
                </Button>
              )}
            </div>
          </div>
          
          {/* Navigation Buttons Row */}
          <nav className="flex flex-wrap gap-3 w-full">
            <ul className="flex flex-wrap justify-start gap-3 w-full">
              <NavItem to="/" icon={<Users className="w-4 h-4" />} label="Guests" />
              <NavItem to="/constraints" icon={<ClipboardList className="w-4 h-4" />} label="Constraints" />
              <NavItem to="/tables" icon={<Table className="w-4 h-4" />} label="Tables" />
              <NavItem to="/assignments" icon={<MapPin className="w-4 h-4" />} label="Assignments" />
              <NavItem to="/seating" icon={<MapPin className="w-4 h-4" />} label="Seating" />
              <NavItem to="/saved-settings" icon={<FolderOpen className="w-4 h-4" />} label="Saved Settings" />
            </ul>
          </nav>
        </div>
      </div>

      {showAuthModal && (
        <AuthModal onClose={() => setShowAuthModal(false)} />
      )}
      
      {showPremiumModal && user && (
        <PremiumModal 
          onClose={() => setShowPremiumModal(false)} 
          userId={user.id}
          onSuccess={handlePremiumSuccess}
        />
      )}
    </header>
  );
};

interface NavItemProps {
  to: string;
  icon: React.ReactNode;
  label: string;
}

const NavItem: React.FC<NavItemProps> = ({ to, icon, label }) => {
  return (
    <li className="flex-none">
      <NavLink 
        to={to} 
        className={({ isActive }) => 
          `flex items-center px-3 py-1 rounded-md transition-colors text-sm border border-[#7973BB] min-w-[110px] whitespace-nowrap
           ${isActive 
             ? 'bg-[#7973BB] text-white' 
             : 'bg-white text-[#7973BB] hover:bg-[#7973BB]/20'}`
        }
      >
        {icon}
        <span className="ml-1">{label}</span>
      </NavLink>
    </li>
  );
};

export default Header;