import React from 'react';
import { ClipboardList, Crown } from 'lucide-react';
import Card from '../components/Card';
import { useApp } from '../context/AppContext';
import { isPremiumSubscription } from '../utils/premium';

const AssignmentManager: React.FC = () => {
  const { state, dispatch } = useApp();
  
  // Check if user has premium subscription
  const isPremium = isPremiumSubscription(state.subscription);
  
  const handleUpdateAssignment = (name: string, value: string) => {
    // For premium users, we need to handle both table names and numbers
    if (isPremium) {
      // Process the input to convert any table names to table numbers
      const processedValue = value.split(',').map(item => {
        const trimmed = item.trim();
        if (!trimmed) return '';
        
        // If it's a number, keep it as is
        if (!isNaN(Number(trimmed))) return trimmed;
        
        // If it's a name, try to find the matching table
        const matchingTable = state.tables.find(
          table => (table.name && table.name.toLowerCase() === trimmed.toLowerCase())
        );
        
        return matchingTable ? matchingTable.id.toString() : trimmed;
      }).filter(Boolean).join(', ');
      
      dispatch({
        type: 'UPDATE_ASSIGNMENT',
        payload: { name, tables: value }
      });
    } else {
      // For non-premium users, keep the original behavior
      dispatch({
        type: 'UPDATE_ASSIGNMENT',
        payload: { name, tables: value }
      });
    }
  };
  
  const getTableList = () => {
    if (!isPremium || !state.tables.some(t => t.name)) {
      return state.tables.map(t => t.id).join(', ');
    }
    
    // For premium users with renamed tables, show both IDs and names
    return state.tables.map(t => {
      if (t.name) {
        return `${t.id} (${t.name})`;
      }
      return t.id;
    }).join(', ');
  };
  
  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-bold text-[#7973BB] flex items-center">
        <ClipboardList className="mr-2" />
        Assignment Manager
        {isPremium && (
          <span className="flex items-center bg-yellow-500 text-black px-2 py-0.5 rounded-full text-sm font-medium ml-2">
            <Crown className="w-4 h-4 mr-1" />
            Premium
          </span>
        )}
      </h1>
      
      <Card>
        <div className="space-y-4">
          <p className="text-[#7973BB]">
            Specify which tables each guest can be assigned to. Enter table numbers separated by commas, or leave blank for automatic assignment.
          </p>
          
          {isPremium && (
            <p className="text-sm text-[#7973BB] bg-green-50 p-3 rounded-md border border-green-300">
              <Crown className="inline-block w-4 h-4 mr-1 text-yellow-500" />
              <strong>Premium feature:</strong> You can enter either table numbers or the exact table names you've created.
            </p>
          )}
          
          <p className="text-sm text-[#7973BB] bg-indigo-50 p-3 rounded-md">
            <strong>Tip:</strong> You can assign a guest to multiple tables by entering comma-separated numbers (e.g., "1,3,5").
            This means the seating algorithm will place them at one of these tables.
          </p>
        </div>
      </Card>
      
      <Card title="Guest Assignments">
        {state.guests.length === 0 ? (
          <p className="text-gray-500 text-center py-4">No guests added yet. Add guests to create assignments.</p>
        ) : (
          <div className="space-y-6">
            {state.guests.map((guest, index) => (
              <div key={index} className="flex flex-col sm:flex-row items-start sm:items-center gap-4 mb-6">
                <div className="min-w-[150px] font-medium text-[#7973BB]">
                  {guest.name}
                  {guest.count > 1 && (
                    <span className="ml-2 text-sm text-gray-500">
                      ({guest.count} people)
                    </span>
                  )}
                </div>
                
                <div className="w-full sm:w-auto">
                  <label 
                    htmlFor={`assignment-${index}`} 
                    className="sr-only"
                  >
                    Table assignment for {guest.name}
                  </label>
                  <div className="relative">
                    <input
                      id={`assignment-${index}`}
                      type="text"
                      value={state.assignments[guest.name] || ''}
                      onChange={(e) => handleUpdateAssignment(guest.name, e.target.value)}
                      placeholder={isPremium ? "Enter table numbers or table names..." : "Enter table numbers..."}
                      className="w-full sm:w-[280px] px-3 py-2 border border-[#7973BB] rounded-md focus:outline-none focus:ring-2 focus:ring-[#7973BB]"
                    />
                    <div className="absolute inset-y-0 right-0 flex items-center pr-3 pointer-events-none">
                      {/* "Table #" text removed from here */}
                    </div>
                  </div>
                  
                  {/* Available tables reminder */}
                  {state.tables.length > 0 && (
                    <div className="mt-1 text-xs text-[#7973BB]">
                      Available tables: {getTableList()}
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </Card>
    </div>
  );
};

export default AssignmentManager;